

.swiper-slide {
  position: relative;

  .colorBg {

    position: absolute;
    top: 50%;
    left: 50%;

    transform-origin: center;
    transition: 1s ease-out;
    border-radius: 50%;


    &.active {
      animation-name: scale;
      animation-fill-mode: both;
      animation-duration: 1s;
      width: 100%;
          height: 200%;
          opacity: 1;
          transform: translate(-50%, -50%) scale(2);

      @keyframes scale {
        from {
          width: 0%;
          height: 0%;
          opacity: 0;
          transform: translate(-50%, -50%) scale(0);
        }

        to {
          width: 100%;
          height: 200%;
          opacity: 1;
          transform: translate(-50%, -50%) scale(2);

        }
      }
    }
  }
  .bg {
    
    position: absolute;
    top: 50%; transform: translateY(-50%);
    background-color: rgb(255, 255, 255,.5);
    width: 100%; height: 350px;


    &.active {
      animation: bg 0.5s cubic-bezier(0, 0.36, 0.58, 1) 0.5s both;

      @keyframes bg {
        from {
          
          opacity: 0;
          right: -100%;

        }
        to {
          
          opacity: 1;
          right: 0;
        }
      }

    }
  }
}


