@import '_reset','common';

.contact {
  background-color: aliceblue;

  .line {
    width: 100%; 
    text-align: right;
  }
  hr {
    width: 180px; border: 3px solid $pink; float: right;
  }

  .content {
    width: 480px;
    // height: 280px;
    text-align: right;
    position: absolute;
    top: 50%;
    left: 24%;
    transform: translateY(-30%);

    section.active {

      &1 {
        animation: slideRight 0.8s ease-out 0.1s both;
      }
      &2 {
        animation: slideRight 0.8s ease-out 0.25s both;
      }
      &3 {
        animation: slideRight 0.8s ease-out 0.4s both;
      }

      @keyframes slideRight {
        from {
          transform: translateX(-700%);
        }
        to {
          transform: translateX(0);
        }
      }
    }

    p {
      font-size: 1.3rem;
      text-align: center;
    }

    span {
      font-size: 1rem;
    }

    button {
      @include btn;
      width: 120px;
      margin: 0;
      position: relative;
      overflow: hidden;
      &:active {
        top: 2px;
      }
      
      &::before {
        background-color: $pink;
         content: "" ; width: 100%; height: 100%; position: absolute; top: 0%; left: 0%; transition: 0.2s;  opacity: 0; z-index: -1;
      }
      
      &:hover {
        background: none;
        &::before {
          height: 100%; opacity: 1;
        }
        border-color:black;
      }
    }
  }
.slide {
  width: 950px;
  // height: 950px;
  border-radius: 50%;
  background-color: #ddd;
  position: absolute;
  right: -150px;
  bottom: -120px;
  box-shadow: 5px 15px 16px 1px $main-color;

  &.active {
    animation: slideWidth 0.5s ease-in-out .1s both;
    transform-origin: bottom;
  }

  @keyframes slideWidth {
    from {
      transform: scale(0);
    }

    to {
      transform: scale(1);
    }

  }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;

   

    }

    img[alt='photo1'] {
      transform: translate(-70px,-120px);
    }
    img[alt='photo2'] {
      transform: translate(0px,-120px);
    }
    img[alt='photo3'] {
      transform: translate(-80px,0px);
    }
    img[alt='photo4'] {
      transform: translate(00px,0px);
    }
    img[alt='photo5'] {
      transform: translate(-50px,0px) rotate(1deg);
    }
    img[alt='photo6'] {
      transform: translateY(-120px);
    }
    img[alt='photo7'] {
      transform: translateY(-120px);
    }
  }

  .goal {
    line-height: 200%;
    @include f2;
  }
}
