@import '_reset';

$main-color : rgb(159, 180, 255);
$main-colorT : rgb(159, 180, 255,.5);
$main-colorTT : rgb(159, 180, 255,.2);
$pink : #FFDDDD;
$yellow : rgb(246, 250, 0);
// $pageJ : #731717;
$pageJ : #D9C0AB;
$pageJT :rgb(217, 192, 171,.8);
$pageR : #C9E2F2;
$pageRT : rgb(201, 226, 242,.8);
$page7 :#F2A285;
$page7T :rgb(242, 162, 133,.8);
$pageC :#8C683C;
$pageCT :rgb(140, 104, 60,.8);
$pageP :  #1B4032 ;
$pagePT :  rgb(27, 64, 50,.8) ;



@mixin btn {
    padding: 8px 18px; margin: 5px 5px 0px 0;
    border-radius: 10px;
    border: 1px solid ;
    box-shadow: 0 2px 0 0 rgb(51, 51, 51,.5);
    background-color: rgb(255, 255, 255,.5);
    cursor: pointer;
}

.titleWrap {
  overflow: hidden;
}

.title {
  display: inline-block;
  color: $main-color;
  font-size: 2rem;
  // border-bottom: 3px solid $pink;
  padding-bottom: 5px;

  .dot {
    background-color: $pink;
  }

  &.active {
    animation: pop 0.3s cubic-bezier(0.22, 0.61, 0.43, 1.29) 0.3s both;
    
    @keyframes pop {
      from {
        transform: translateY(150%);
      }
      to {
        transform: translateY(0);
      }
      
    }
  }
}

h1 {
  line-height: 90%;
}

.container {
  // transform: translateY(-40px);
  position: relative;
  width: 1480px;
  height: 800px;
  margin: 0 auto;
}

nav {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 999;
  width: 100%; 
  text-align: center;

  &.slideDown {
    animation: down 0.3s ease-in-out 0.3s both;
    
    @keyframes down {
      from {
        top: -50px;
      }
      to {
        top: 0;
      }
    }
  }

  &.hide {
    top: -50px;
  }

  .menu {
    width: 100%;  border-bottom: 1px solid rgb(255, 255, 255,.5); height: 50px; display: flex; justify-content: flex-end;
    
      >li {
        cursor: pointer;
        font-weight: bold;
        float: left; width: 150px; height: 50px; color: white; 
        line-height: 50px;


        &:hover {
          background-color: rgb(255, 255, 255,.3);
          // color:$main-color;
        }
        
        &:last-child {
          margin-right: 50px;
        }
      
        .submenu {


          li {
            color: white;
            font-weight: normal;
            background-color: rgb(255, 255, 255,.3);
            height: 40px;
            line-height: 40px;

            &:hover {
              // background-color:$main-colorT;
              color: white;
            }
          }
          
        }
      
      }

      
  
  }
  .menu.color {
    border-bottom: 1px solid $main-colorT;

    >li {
      color: $main-color;

      &:hover {
        background-color: $main-colorTT;
      }

      .submenu {
        background-color: $main-colorT;
      }
    }

  }

  .active {
      background-color: $main-colorTT;
  }

  .active2 {
    background-color: rgb(255, 255, 255,.2);
    
    color: $pageJ !important;
    .active {
      background-color :$pageJT !important;
    }
  }
  .active3 {
    background-color: rgb(255, 255, 255,.2);
    
    color: $pageR !important;
    .active {
      background-color: $pageRT !important;
    }
  }
  .active4 {
    background-color: rgb(255, 255, 255,.2);
    
    color:  $page7 !important;
    .active {
      background-color:  $page7T !important;
    }
  }
  .active5 {
    background-color: rgb(255, 255, 255,.2);
    
    color:  $pageC !important;
    .active {
      background-color:  $pageCT !important;
    }
  }
  .active6 {
    background-color: rgb(255, 255, 255,.2);
    
    color:  $pageP !important;
    .active {
      background-color: $pagePT !important;
    }
  }

  .submenu {
    display: none;
    overflow: hidden;
  }
  @keyframes slideDown {
    from {
      max-height: 0;
    }
    to {
      max-height: calc(40px * 5);
    }
    
  }
  .submenu.open {
    overflow: hidden;
    height: auto;
    display: block !important;
    animation: slideDown 0.5s ease forwards;
  }


}

.dot {
  width: 7px;
  height: 7px;
  background-color: $main-color;
  border-radius: 50%;
  display: inline-block;
  margin-left: 5px;
}

.particle {
  background-color: #FFF;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

