#app {
  height: 100%;
}
html,
body {
  position: relative;
  height: 100%;
}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.swiper {
  width: 100%;
  height: 100vh;
}

.swiper-slide {
  /* text-align: center; */
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide:nth-of-type(1) {
  background: none

}
.swiper-slide:nth-of-type(2){
  background-color: rgb(255, 255, 255,.8);
}



.swiper-slide img {
  display: block;
  width: 100%;
  /* height: 100%; */
  object-fit: cover;
}

.swiper-vertical > .swiper-pagination-bullets, .swiper-pagination-vertical.swiper-pagination-bullets {
  padding-right: 30px;
  
}


.swiper-pagination-bullet{
  /* background-color:  white;  */
  background-color:  #9FB4FF; opacity: 0.3;
}

.swiper-pagination-bullet-active {
  opacity: 1;
}
