@import '_reset', 'common';

$LIGHTING_FLOOR: 1;
$LIGHTING_CEIL: 2;
$LIGHTING_FLAT: 3;

@mixin text3d($primary,
  $depth: 5,
  $shadowsteps: 5,
  $shadowincrementer: 3px,
  $shadowopacity: .5,
  $primaryshadowcolour: #000,
  $lighting: $LIGHTING_CEIL) {
  $predefinedShadows: (
    0 0 5px rgba($primaryshadowcolour, .05),
    0 -1px 3px rgba($primaryshadowcolour, .2),
    0 3px 5px rgba($primaryshadowcolour, .2)
  );
$value: (
);

@for $i from 1 through $depth {
  $num: $i + px;
  $hueadjust: $i;

  @if($lighting ==$LIGHTING_FLOOR) {
    $hueadjust: (
      $i * 2 - $depth - 5) * 1%;
  }

  @else if ($lighting ==$LIGHTING_CEIL) {
    $hueadjust: -($i*2 + $depth - 10) * 1%;
  }

  @else if ($lighting ==$LIGHTING_FLAT) {
    $hueadjust: -$depth * 1%;
  }

  $colour: adjust-color($primary, $lightness: $hueadjust
  );
$theShadow: 0 $num 0 $colour;
$value: append($value, $theShadow, comma);
}

@for $i from 1 through $shadowsteps {
  @if($i >=length($predefinedShadows)) {
    $dist: $i * $shadowincrementer;
    $value: append($value, 0 $dist $dist rgba($primaryshadowcolour, $shadowopacity));
  }

  @else {
    $value: append($value, nth($predefinedShadows, $i));
  }
}

text-shadow: $value;


@keyframes text_3D {
  from {
    $depth: 0;
    $shadowopacity: 0;
  }

  to {
    $depth: 8;
    $shadowopacity: .3;
  }

}


}

$primarycolour: $main-color;


.intro {
  width: 100%;
  height: 100%;

  .introWrap {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .wrap {
      overflow: hidden;
      width: calc(51%);
      display: flex;
      justify-content: center;
    }

    .text {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 97.2%;
      color: $main-color;

      animation: pop 0.3s cubic-bezier(0.22, 0.61, 0.43, 1.29) 0.7s both;


      @keyframes pop {
        from {
          transform: translateY(150%);
        }

        to {
          transform: translateY(0);
        }

      }

      p {
        font-size: 20px;
      @include f2;

        
      }

      hr {
        width: 53%;
        border: 1px solid $main-color;
      }

      span {
        font-weight: bold;
      @include f2;

      }
    }

    .h1wrap {
      overflow: hidden;
      padding: 10px 5px 15px 5px;
    }

    h1 {

      text-align: center;
      color: $main-color;
      font-size: 100px;
      line-height: 1em;
      margin: 0;
      text-align: center;
      width: 100%;
      // animation: text_3D 0.5s ease 0.5s infinite;
      animation: pop 0.3s cubic-bezier(0.22, 0.61, 0.43, 1.29) 0.3s both;

      @keyframes pop {
        from {
          transform: translateY(150%);
        }

        to {
          transform: translateY(0);
        }

      }

      @include text3d(adjust-color($primarycolour, $lightness: -8%),
        $depth: 8,
        $primaryshadowcolour: adjust-color($primarycolour, $lightness: -10%, $saturation: +20%),
        $shadowopacity: 0.3);


    }


  }

  .scroll {
    position: absolute;
    bottom: 80px;
    display: flex;
    flex-direction: column;
    color: $main-color;
    opacity: 0.8;
    font-size: .9rem;
    align-items: center;
    animation: scrollOpacity 0.5s ease-in 1s both, scroll 0.4s ease-in 2s infinite alternate;

    p {
      margin-bottom: 20px;
    }

    @keyframes scrollOpacity {
      from {
        opacity: 0;
      }

      to {
        opacity: 1;
      }
    }
  }

  @keyframes scroll {
    from {
      bottom: 80px;
    }
    to {
      bottom: 100px;
    }

  }


}

