@import '_reset','common';

.project {

  .title h1 {
    color: white;
  }

  .content {
    // margin: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    // transform: translateY(-20px);

    .imgBox {
      position: relative;
      margin-right: 150px;

      .none {
        display: none;
      }

      img {
        width: 100%;
        // height: 100%;
        object-fit: cover;
        transform-origin: top;

        &:hover {
          transform: translateY(0);
        }

      }


      .imgPc {
        width: 850px;
        height: 540px;
        border-radius: 20px 60px;
        overflow: hidden;
        box-shadow: 5px 5px 15px 2px #0000004a;
        cursor: pointer;

        >.active {
          transform: translateY( calc(-100% + 541px));
          transition: 3s ease-in-out;
        }
      }

      .imgT {
        width: 240px;
        height: 350px;
        border-radius: 15px;
        overflow: hidden;
        position: absolute;
        bottom: -20px;
        right: -100px;
        

        .ipad {
          width: 100%;
          height: 100%;
          position: relative;

          .active { 
            transform: translateY( calc(-100% + 350px));
            transition: 2s ease-in-out;
          }

          .ipadM {
            width: 100%;
            height: 100%;
            object-fit: contain;
            z-index: 5;


          }

          .screen {
            width: 90%;
            height: 89%;
            border-radius: 5px;
            // background-color: pink;
            position: absolute;
            top: 20px;
            left: 50%;
            transform: translate(-50%, 0%);
            cursor: pointer;

            .active { 
              transform: translateY( calc(-100% + 311px));
              transition: 2s ease-in-out;
            }

            

            img {
              width: 100%;
              // height: 100%;
            }

            overflow: hidden;
          }

        }


      }

      .imgM {
        width: 135px;
        height: 270px;
        border-radius: 20px;
        overflow: hidden;
        position: absolute;
        bottom: -40px;
        right: 80px;

        .iphone {
          width: 100%;
          height: 100%;
          position: relative;

          .iphoneM {
            width: 100%;
            height: 100%;
            object-fit: contain;
            z-index: 5;
          }

          .screen {
            width: 92%;
            height: 97%;
            border-radius: 17px;
            // background-color: pink;
            position: absolute;
            top: 5px;
            left: 50%;
            transform: translate(-50%, 0%);
            cursor: pointer;

            .active { 
              transform: translateY( calc(-100% + 261px));
              transition: 2s ease-in-out;
            }
            img {
              width: 100%;
              // height: 100%;
            }

            overflow: hidden;
          }

        }


      }

    }

    .subBox {
      width: 400px;
      height: 350px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 10px 0;
      margin-right: 80px;
      // transform: translateY(-22px);

      h3 {
        font-size: 1.2rem;
        @include f2;
        font-weight: 100;
      }

      hr {
        margin: 10px 0 10px 0; width: 30%;
        border: 1px solid ;
        opacity: .2;

      }
      dl {
        display: flex;

        dt {
          width: 35%;
          font-weight: bold;
        }

        dd {
          width: 65%;
        }
      }

      .btnBox {
        button {
          @include btn;
          position: relative;
          overflow: hidden;


          &:active {
            top: 2px;
          }
          
          &::before {
             content: "" ; width: 100%; height: 100%; position: absolute; top: 0%; left: 0%; 
              z-index: -1;
          }

          &:hover {
            background: none;
            &::before {
              height: 100%; opacity: 1;
            }


          &.btn1 {
            &::before {
              background-color: $pageJ !important;

            }
          }
          &.btn2 {
            &::before {
              background-color: $pageR;
            }
            // &:hover {
            //   background-color: $pageR !important;

            // }
          }
          &.btn3 {
            &::before {
              background-color: $page7;
            }
            // &:hover {
            //   background-color: $page7 !important;

            // }
          }
          &.btn4 {
            &::before {
              background-color: $pageC;
            }
            &:hover {
              // background-color: $pageC !important;
              color: white;
              border-color: black;


            }
            :active {
              top: 2px;
            }
          }
          &.btn5 {
            &::before {
              background-color: $pageP;
            }
          
              
              color: white;
              border-color: black;
            }
          }

          
        }
        
      }
    }
  }




}
