@import 'common';

.aboutMe {
  .content {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    justify-content: space-between;
    height: 63%;
    align-items: center;

    .lbox {
      width: 48%;position: relative; height: 100%; display: flex; align-items: flex-end; padding-left: 5.6rem; 
      
      h2 {
        width: 100%;
      }
  
      .profile {
        width: 450px;
        height: 450px;
        border-radius: 50%;
        background-color: #ddd;
        position: absolute;
        top: 0;
        right: 0;
        overflow: hidden;

        img {
          width: auto;
          height: 100%;
          object-fit: cover;
          transform: translateX(-35px) scaleX(-1);
          position: relative;
          
          
        }
  &::after {
            content: ""; width: 100%; height: 100%; position: absolute; top: 0; left: 0; background-color: #eee; opacity: 0.05;
          }
      }
  
      .text {
        z-index: 10;
        position: relative;
        mix-blend-mode: overlay;

  
        h3 {
        @include f2;

          font-size: 6rem;
          font-weight: bold;
          color: #333;
  
  
        }
  
        p {
          font-size: .8rem;
          // width: 80%;
        }
      }
  
    }
    .rbox {
      width: 45%;
      hr {
        width: 195px; border: 3px solid $pink
      }
      h3 {
        margin-bottom: 10px;

        
      }

      section.active {
        &1 {
          animation: slideLeft 1s ease-out 0.1s both;
        }
        &2 {
          animation: slideLeft 1s ease-out 0.25s both;
        }
        &3{
          animation: slideLeft 1s ease-out 0.4s both;
        }

        

        @keyframes slideLeft {
          from {
            transform: translateX(700%);
          }
          to {
            transform: translateX(0);
          }
        }
      }
      p{
        margin-bottom: 10px;
        line-height: 180%;

      }

      .skillbox {
        >p{
          background-color: $pink;
        border-radius: 20px; 
        padding: 0px !important;
        transition: 0.5s;
        max-height: 0;
        overflow: hidden;
        line-height: 150%;
        font-size: 1rem;
        @include f2;

        &.active {
          max-height: 120px;
          height: 120px;
          padding: 20px 25px !important;
          display: flex;
          align-items: center;
        }
        }
        

        .icon {
          display: flex; justify-content: space-between;
        }
        .skill {
          position: relative;
          // cursor: pointer;

          &:nth-of-type(5) {
            img {
              object-fit: contain;
            }
          }

          .logo {
            cursor: pointer;
            width: 50px; height: 50px; 
            background-color: rgb(238, 238, 238,.5); 
            border-radius: 50%;

            img {
              width: 100%;
              height: 100%;
              transform: scale(0.5);
              transform-origin: center;
              
            }

          }
          svg {
            position: absolute;
            top: 0;

          }
          .stroke {
            fill: transparent;
            stroke: $main-color;
            stroke-width: 5px;
            transform: rotate(-90deg);
            transform-origin: center;
          }
          p {
            font-size: .8rem; text-align: center;
            @include f2; font-weight: lighter; color: #68687a;
          }
          
          &:nth-of-type(1) {
            .stroke {
              stroke-dasharray: 130;
            }
          }
          &:nth-of-type(2) {
            .stroke {
              stroke-dasharray: 130;
            }
          }
          &:nth-of-type(3) {
            .stroke {
              stroke-dasharray: 90;
            }
          }
          &:nth-of-type(4) {
            .stroke {
              stroke-dasharray: 130;
            }
          }
          &:nth-of-type(5) {
            .stroke {
              stroke-dasharray: 110;
            }
          }
          &:nth-of-type(6) {
            .stroke {
              stroke-dasharray: 70;
            }
          }
          &:nth-of-type(7) {
            .stroke {
              stroke-dasharray: 130;
            }
          }
          &:nth-of-type(8) {
            .stroke {
              stroke-dasharray: 110;
            }
          }
        }
      }
    }

  }
  

}
